import React, { useState, useContext, useEffect, useCallback, useRef } from 'react';
import {
  IonButtons, IonContent, IonHeader, IonMenuButton, IonPage, IonTitle, IonToolbar, IonRange,
  IonButton, IonInput, IonImg, IonPopover, IonIcon, useIonAlert
} from '@ionic/react';

import {
  AppContext, setFrequencyAction, setAmplitudeAction, setWaveformAction,
  setPhaseAction, setCurrChannelAction, setChannelOutputtingAction,
  getFrequency, getAmplitude, getWaveform, getPhase, getActiveChannel,
  getTargetChannel, getOutputting, acceptWarning,
} from '../State';

import type { WAVETYPE } from '../State';

import { FunctionGeneratorAudio } from '../components/FunctionGeneratorAudio';
import './FunctionGenerator.css';
import { isPlatform } from '@ionic/core';

const FunctionGenerator: React.FC = () => {

  const { state, dispatch } = useContext(AppContext);
  const [present] = useIonAlert();

  // Ear damage warning
  useEffect(() => {
    if(state.earWarning) {
      present({
        cssClass: 'alert',
        header: 'Warning',
        message: 'This app can produce loud sounds that can damage sensitive parts of the ear resulting in hearing loss.<br /><br />Use with caution and avoid wearing headphones.',
        buttons: [ 
          {
            text: 'I understand',
            handler: () => {
              dispatch(acceptWarning());
            }
          }
        ]
      });
    }
  }, [dispatch, present, state.earWarning]);


  // Effect for disabling channels outputting when leaving page
  useEffect(() => {
    // Cleanup
    return () => {
      dispatch(setChannelOutputtingAction(0, false));
      dispatch(setChannelOutputtingAction(1, false));
    }
  }, [dispatch]);

  const name = "Function Generator";

  // Selected channel obj
  const activeChannel = getActiveChannel(state);

  // Left-right channels
  const chOne = getTargetChannel(state, 0);
  const chTwo = getTargetChannel(state, 1);

  // Get and set frequency
  const frequency = getFrequency(activeChannel);
  const setFrequency = useCallback((freq: number) => {
    dispatch(setFrequencyAction(freq));
  }, [dispatch]);

  // Get both channel frequencies
  const chOneFrequency = getFrequency(chOne);
  const chTwoFrequency = getFrequency(chTwo);

  // Get and set amplitude
  const amplitude = getAmplitude(activeChannel);
  const setAmplitude = useCallback((amp: number) => {
    dispatch(setAmplitudeAction(Math.max(0, amp)));
  }, [dispatch]);

  // Get both channel amplitude
  const chOneAmplitude = getAmplitude(chOne);
  const chTwoAmplitude = getAmplitude(chTwo);

  // Get and set waveform
  const waveform = getWaveform(activeChannel);
  const setWaveform = useCallback((waveform: WAVETYPE) => {
    dispatch(setWaveformAction(waveform));
  }, [dispatch]);

  const chOneWaveform = getWaveform(chOne);
  const chTwoWaveform = getWaveform(chTwo);

  // Get and set phase
  const phase = getPhase(activeChannel);
  const setPhase = useCallback((phase: number) => {
    dispatch(setPhaseAction(phase));
  }, [dispatch]);

  const chOnePhase = getPhase(chOne);
  const chTwoPhase = getPhase(chTwo);

  // Get and set current channel
  const currChannel = state.currChannel;
  const setCurrChannel = useCallback((channel: 0 | 1) => {
    dispatch(setCurrChannelAction(channel));
  }, [dispatch]);

  // Get and set channels outputting
  const chOneOutputting = getOutputting(chOne);
  const chTwoOutputting = getOutputting(chTwo);

  const toggleChOneOutputting = useCallback(() => {
    dispatch(setChannelOutputtingAction(0, !chOneOutputting));
  }, [chOneOutputting, dispatch]);

  const toggleChTwoOutputting = useCallback(() => {
    dispatch(setChannelOutputtingAction(1, !chTwoOutputting));
  }, [chTwoOutputting, dispatch]);

  // parsing frequency slider value to change it to frequency value
  function logarithmic(value: number) {
    let frequencyValue = Math.exp(value) + 19;
    setFrequency(frequencyValue);
  }

  // For showing and hiding popovers  
  const [frequencyPopoverState, setShowFrequencyPopover] = useState({ showPopover: false, frequency: 440 });
  const [amplitudePopoverState, setShowAmplitudePopover] = useState({ showPopover: false, amplitude: 100 });
  const [phasePopoverState, setShowPhasePopover] = useState({ showPopover: false, phase: 180 });

  function popoverFrequencySet() {
    setFrequency(frequencyPopoverState.frequency);
    setShowFrequencyPopover({ showPopover: false, frequency: frequency });
  }

  function popoverAmplitudeSet() {
    setAmplitude(amplitudePopoverState.amplitude);
    setShowAmplitudePopover({ showPopover: false, amplitude: amplitude });
  }

  function popoverPhaseSet() {
    setPhase(phasePopoverState.phase);
    setShowPhasePopover({ showPopover: false, phase: phase });
  }

  function keyUpHandler(event: React.KeyboardEvent<HTMLIonInputElement>, popover: string) {
    if (event.key === "Enter") {
      switch (popover) {
        case "frequency": popoverFrequencySet(); break;
        case "amplitude": popoverAmplitudeSet(); break;
        case "phase": popoverPhaseSet(); break;
      }
    }
  }

  const inputFrequency = useRef<HTMLIonInputElement>(null!);
  const inputAmplitude = useRef<HTMLIonInputElement>(null!);
  const inputPhase = useRef<HTMLIonInputElement>(null!);

  function frequencyInputFocus() {
    inputFrequency.current.setFocus();
  }

  function amplitudeInputFocus() {
    inputAmplitude.current.setFocus();
  }

  function phaseInputFocus() {
    inputPhase.current.setFocus();
  }

  const chOneValues = { active: chOneOutputting, frequency: chOneFrequency, messageFrequency: 0, amplitude: chOneAmplitude / 100, phase: chOnePhase, waveform: chOneWaveform, modulation: false}
  const chTwoValues = { active: chTwoOutputting, frequency: chTwoFrequency, messageFrequency: 0, amplitude: chTwoAmplitude / 100, phase: chTwoPhase, waveform: chTwoWaveform, modulation: false}

  return (
    <IonPage>
      <IonHeader>
        <IonToolbar mode="ios" color="primary">
          <IonButtons slot="start">
            <IonMenuButton />
          </IonButtons>
          <IonTitle><b style={{fontWeight: "bolder"}}>Husker Scope</b>{": " + name}</IonTitle>
          <IonImg slot="end" class="logo" src={process.env.PUBLIC_URL + "/assets/Husker_Scope_Logo_circle.png"}></IonImg>
        </IonToolbar>
      </IonHeader>

      <IonContent fullscreen scrollY={false} color="secondary">
        <FunctionGeneratorAudio chOne={chOneValues} chTwo={chTwoValues} />
        <div className="frequency-waveform-channels">

          {/* Channel 1 Editing Button */}
          <div className="channel-div">
            <IonButton class="edit-button-L" mode="ios" onClick={() => { setCurrChannel(0) }} color={currChannel === 0 ? "primary" : "light"} data-testid="left-edit">
              <div className="edit-button-contents">
                CH1 <br /> FQ: {Math.round(chOneFrequency)} Hz <br /> AMP: {chOneAmplitude} <br /> PH: {chOnePhase}°
                <img className="edit-img" src={process.env.PUBLIC_URL + `/assets/${chOneWaveform}-wave.png`} alt="" />
              </div>
            </IonButton>
          </div>

          <div className="frequency-and-waveform">

            {/* Frequency Button and Slider */}
            <div className="frequency-button-div">
              <IonButton class="frequency-button" mode="ios" color="light" onClick={() => setShowFrequencyPopover({ showPopover: true, frequency: frequency })} data-testid="frequency-button">
                Frequency: {Math.round(frequency)} Hz
              </IonButton>
            </div>

            <div>
              <IonRange class="range-frequency" mode="ios" value={Math.log(frequency - 19)} min={0} max={Math.log(20000 - 19)} step={.0000000001} onIonChange={e => logarithmic(e.detail.value as any)} data-testid="frequency-range"/>
            </div>

            {/* Waveform Buttons */}
            <div className="waveform-buttons">
              <IonButton class="waveform-button" color={waveform === "sine" ? "primary" : "light"} onClick={() => { setWaveform("sine") }} mode="ios" data-testid="sine-button">
                <IonIcon class="waveform-icon-1" slot="icon-only" src={process.env.PUBLIC_URL + "/assets/icon/sine-wave2.svg"} /></IonButton>
              <IonButton class="waveform-button" color={waveform === "triangle" ? "primary" : "light"} onClick={() => { setWaveform("triangle") }} mode="ios" data-testid="triangle-button">
                <IonIcon class="waveform-icon-1" slot="icon-only" src={process.env.PUBLIC_URL + "/assets/icon/triangle-wave2.svg"} /></IonButton>
              <IonButton class="waveform-button" color={waveform === "square" ? "primary" : "light"} onClick={() => { setWaveform("square") }} mode="ios" data-testid="square-button">
                <IonIcon class="waveform-icon-2" slot="icon-only" src={process.env.PUBLIC_URL + "/assets/icon/square-wave2.svg"} /></IonButton>
              <IonButton class="waveform-button" color={waveform === "sawtooth" ? "primary" : "light"} onClick={() => { setWaveform("sawtooth") }} mode="ios" data-testid="sawtooth-button">
                <IonIcon class="waveform-icon-2" slot="icon-only" src={process.env.PUBLIC_URL + "/assets/icon/sawtooth-wave2.svg"} /></IonButton>
            </div>
          </div>

          {/* Channel 2 Editing Button */}
          <div className="channel-div">
            <IonButton class="edit-button-R" mode="ios" onClick={() => { setCurrChannel(1) }} color={currChannel === 1 ? "primary" : "light"} data-testid="right-edit">
              <div className="edit-button-contents">
                CH2 <br /> FQ: {Math.round(chTwoFrequency)} Hz <br /> AMP: {chTwoAmplitude} <br /> PH: {chTwoPhase}°
                <img className="edit-img" src={process.env.PUBLIC_URL + `/assets/${chTwoWaveform}-wave.png`} alt="" />
              </div>
            </IonButton>
          </div>

        </div>

        {/* Amplitude, Start/Stop Buttons, Phase */}
        <div className="amplitude-start-phase">

          {/* Amplitude Button and Slider */}
          <div className="amplitude-components">
            <IonButton class="amplitude-button" mode="ios" color="light" onClick={() => setShowAmplitudePopover({ showPopover: true, amplitude: amplitude })} data-testid="amplitude-button">
              Amplitude: {amplitude}%
            </IonButton>
            <IonRange class="range-amplitude" mode="ios" value={amplitude} min={0} max={100} step={1} onIonChange={e => setAmplitude(e.detail.value as any)} data-testid="amplitude-range"/>
          </div>

          {/* Play/Pause Buttons */}
          <div className="start-buttons">
            {/* CH1 Play/Pause Button */}
            <IonButton class="hold-button" mode="ios" onClick={() => { toggleChOneOutputting() }} color={chOneOutputting ? "primary" : "light"} data-testid="left-play">
              <div className="edit-button-contents">
                CH1
                <img className="play-pause-img" src={process.env.PUBLIC_URL + (chOneOutputting ? "/assets/pause.png" : "/assets/play.png")} alt="" ></img>
              </div>
            </IonButton>

            {/* CH2 Play/Pause Button */}
            <IonButton class="hold-button" mode="ios" onClick={() => { toggleChTwoOutputting() }} color={chTwoOutputting ? "primary" : "light"} data-testid="right-play">
              <div className="edit-button-contents">
                CH2
                <img className="play-pause-img" src={process.env.PUBLIC_URL + (chTwoOutputting ? "/assets/pause.png" : "/assets/play.png")} alt="" ></img>
              </div>
            </IonButton>
          </div>

          {/* Phase Button and Slider */}
          <div className="phase-components">
            <IonButton class="phase-button" mode="ios" color="light" onClick={() => setShowPhasePopover({ showPopover: true, phase: phase })} data-testid="phase-button">
              Phase: {phase}°
            </IonButton>
            <IonRange class="range-phase" mode="ios" value={phase} min={0} max={360} step={1} onIonChange={e => setPhase(e.detail.value as any)} data-testid="phase-range"/>
          </div>

        </div>


        {/* Frequency Popover */}
        <IonPopover
          isOpen={frequencyPopoverState.showPopover}
          cssClass={isPlatform("ios") || isPlatform("android") ? "input-popover" : "input-popover-web"}
          mode="ios"
          showBackdrop={true}
          onDidPresent={() => frequencyInputFocus()}
          onDidDismiss={() => setShowFrequencyPopover({ showPopover: false, frequency: frequency })} data-testid="frequency-popup">
          <div className="popover-content">
            <IonInput ref={inputFrequency} class="input-popover-text" type="number" inputmode="numeric" placeholder="20 to 20000 Hz"
              onIonChange={e => setShowFrequencyPopover((state) => { return { ...state, frequency: parseInt(e.detail.value!, 10) } })}
              onKeyUp={e => keyUpHandler(e, "frequency")}
            ></IonInput>
            <IonButton
              onClick={(e: any) => popoverFrequencySet()} data-testid="frequency-confirm">
              CONFIRM
            </IonButton>
          </div>
        </IonPopover>

        {/* Amplitude Popover */}
        <IonPopover
          isOpen={amplitudePopoverState.showPopover}
          cssClass={isPlatform("ios") || isPlatform("android") ? "input-popover" : "input-popover-web"}
          mode="ios"
          showBackdrop={true}
          onDidPresent={() => amplitudeInputFocus()}
          onDidDismiss={() => setShowAmplitudePopover({ showPopover: false, amplitude: frequency })} data-testid="amplitude-popup">
          <div className="popover-content">
            <IonInput ref={inputAmplitude} class="input-popover-text" type="number" inputmode="numeric" placeholder="0 to 100"
              onIonChange={e => setShowAmplitudePopover((state) => { return { ...state, amplitude: parseInt(e.detail.value!, 10) } })}
              onKeyUp={e => keyUpHandler(e, "amplitude")}
            ></IonInput>
            <IonButton onClick={(e: any) => popoverAmplitudeSet()} data-testid="amplitude-confirm">
              CONFIRM
            </IonButton>
          </div>
        </IonPopover>

        {/* Phase Popover */}
        <IonPopover
          isOpen={phasePopoverState.showPopover}
          cssClass={isPlatform("ios") || isPlatform("android") ? "input-popover" : "input-popover-web"}
          mode="ios"
          showBackdrop={true}
          onDidPresent={() => phaseInputFocus()}
          onDidDismiss={() => setShowPhasePopover({ showPopover: false, phase: phase })} data-testid="phase-popup">
          <div className="popover-content">
            <IonInput ref={inputPhase} class="input-popover-text" type="number" inputmode="numeric" placeholder="0° to 360°"
              onIonChange={e => setShowPhasePopover((state) => { return { ...state, phase: parseInt(e.detail.value!, 10) } })}
              onKeyUp={e => keyUpHandler(e, "phase")}
            ></IonInput>
            <IonButton onClick={() => popoverPhaseSet()} data-testid="phase-confirm">
              CONFIRM
            </IonButton>
          </div>
        </IonPopover>
      </IonContent>
    </IonPage>
  );
};

export default FunctionGenerator;

