import React, { createContext, useEffect } from "react";
import { clamp } from 'utils/MathUtils';

// Consts
const MIN_PHASE = 0;
const MAX_PHASE = 360;

// Type definitions

// Total app state
interface AppStateInterface {
  channelOne: FuncGenChannelInterface;
  channelTwo: FuncGenChannelInterface;
  currChannel: 0 | 1;
  earWarning: boolean;
}

// FG channel state
interface FuncGenChannelInterface {
  frequency: number;
  carrierFrequency: number;
  messageFrequency: number;
  amplitude: number;
  waveform: WAVETYPE;
  phase: number;
  outputting: boolean;
  modulation: boolean;
}

// Context including state and a dispatcher
interface AppContextInterface {
  state:AppStateInterface;
  dispatch:React.Dispatch<ACTIONTYPE>;
}

// Type for component accepting child props
interface AppProps {
  children: React.ReactNode;
}

// Waveform types
export type WAVETYPE = "square" | "sawtooth" | "sine" | "triangle" | "custom";

// Reducer action types
type ACTIONTYPE =
  | { type: "acceptWarning"; }
  | { type: "setFrequency"; payload: number; }
  | { type: "setCarrierFrequency"; payload: number; }
  | { type: "setMessageFrequency"; payload: number; }
  | { type: "setAmplitude"; payload: number; }
  | { type: "setWaveform"; payload: WAVETYPE;}
  | { type: "setPhase"; payload: number;}
  | { type: "setChannelOutputting"; channel: 0 | 1; payload: boolean;}
  | { type: "setCurrChannel"; payload: 0 | 1;}
  | { type: "setModulationMode"; payload: boolean;};

// Default state for a channel
const defaultChannelState: FuncGenChannelInterface = {
  frequency: 440,
  carrierFrequency: 550,
  messageFrequency: 20,
  amplitude: 100,
  waveform: "sine",
  phase: 180,
  outputting: false,
  modulation: true
};

// Default total app state
const initialState: AppStateInterface = {
  channelOne: defaultChannelState,
  channelTwo: defaultChannelState,
  currChannel: 0,
  earWarning: true,
};

export const AppContext: React.Context<AppContextInterface> = createContext<AppContextInterface>({} as AppContextInterface);

// Load state or default to empty persisted state
const persistedState: AppStateInterface = JSON.parse(window.localStorage['persistedState'] ?? '{}');

// Helper function to apply a change to a channel
const modifyChannelState = (state: AppStateInterface, channelData: Partial<FuncGenChannelInterface>, onChannel = state.currChannel): AppStateInterface => {
  // Shallow copy state
  let newState = {...state};

  // Get property name we are modifiying
  let targetChannel: keyof AppStateInterface = onChannel === 0 ? 'channelOne' : 'channelTwo';

  // Shallow copy target channel and replace given data
  let channelObj = {...state[targetChannel], ...channelData};

  return {...newState, [targetChannel]: channelObj};
}

const reducer: React.Reducer<AppStateInterface, ACTIONTYPE> = (state: AppStateInterface, action: ACTIONTYPE) => {
  switch(action.type) {
    case "acceptWarning": {
      return {...state, earWarning: false};
    }
    case "setFrequency": {
      return modifyChannelState(state, {frequency: action.payload});
    }
    case "setCarrierFrequency": {
      return modifyChannelState(state, {carrierFrequency: action.payload});
    }
    case "setMessageFrequency": {
      return modifyChannelState(state, {messageFrequency: action.payload});
    }
    case "setAmplitude": {
      return modifyChannelState(state, {amplitude: action.payload});
    }
    case "setWaveform": {
      return modifyChannelState(state, {waveform: action.payload});
    }
    case "setPhase": {
      return modifyChannelState(state, {phase: clamp(action.payload, MIN_PHASE, MAX_PHASE)});
    }
    case "setChannelOutputting": {
      return modifyChannelState(state, {outputting: action.payload}, action.channel);
    }
    case "setCurrChannel": {
      return {...state, currChannel: action.payload};
    }
    case "setModulationMode": {
      return modifyChannelState(state, {modulation: action.payload});
    }
  }

  // Action not handled
  return state;
};

export function AppContextProvider(props : AppProps) {
  // Deeply merge the initial state with persisted
  const fullInitialState = {
    ...initialState,
    ...persistedState,
    channelOne: {
      ...initialState.channelOne,
      ...persistedState.channelOne,
    },
    channelTwo: {
      ...initialState.channelTwo,
      ...persistedState.channelTwo,
    },
  };
  
  let [state, dispatch] = React.useReducer(reducer, fullInitialState);

  useEffect(() => {
    // Explicitly serialize these parts of state.
    // This allows for modifying persistence behavior for particular state.
    const toSerialize: AppStateInterface = {
      ...state,
      channelOne: {
        ...state.channelOne,
        outputting: false,
      },
      channelTwo: {
        ...state.channelTwo,
        outputting: false,
      },
      currChannel: 0,
    };
    window.localStorage['persistedState'] = JSON.stringify(toSerialize);
  }, [state]);


  let value = { state, dispatch }

  return (
    <AppContext.Provider value={value}>{props.children}</AppContext.Provider>
  );
}

export const AppContextConsumer = AppContext.Consumer; 

export const acceptWarning = (): ACTIONTYPE => 
({
  type: 'acceptWarning',
});

export const setFrequencyAction = (frequency: number): ACTIONTYPE =>
({
  type: 'setFrequency',
  payload: frequency
});

export const setCarrierFrequencyAction = (messageFrequency: number): ACTIONTYPE =>
({
  type: 'setCarrierFrequency',
  payload: messageFrequency
});

export const setMessageFrequencyAction = (messageFrequency: number): ACTIONTYPE =>
({
  type: 'setMessageFrequency',
  payload: messageFrequency
});

export const setAmplitudeAction = (amplitude: number): ACTIONTYPE =>
({
  type: 'setAmplitude',
  payload: amplitude
});

export const setWaveformAction = (waveform: WAVETYPE): ACTIONTYPE =>
({
  type: 'setWaveform',
  payload: waveform
});

export const setPhaseAction = (phase: number): ACTIONTYPE =>
({
  type: 'setPhase',
  payload: phase
});

export const setCurrChannelAction = (channel: 0 | 1): ACTIONTYPE =>
({
  type: 'setCurrChannel',
  payload: channel
});

export const setChannelOutputtingAction = (channel: 0 | 1, outputting: boolean): ACTIONTYPE =>
({
  type: 'setChannelOutputting',
  payload: outputting,
  channel: channel,
});

export const setModulationModeAction = (modulation: boolean): ACTIONTYPE =>
({
  type: 'setModulationMode',
  payload: modulation,
});

// Get current channel object
export const getActiveChannel = (state: AppStateInterface) => (getTargetChannel(state, state.currChannel));
export const getTargetChannel = (state: AppStateInterface, target: 0 | 1) => (target === 0 ? state.channelOne : state.channelTwo);

export const getFrequency = (channel: FuncGenChannelInterface) => (channel.frequency);
export const getCarrierFrequency = (channel: FuncGenChannelInterface) => (channel.carrierFrequency);
export const getMessageFrequency = (channel: FuncGenChannelInterface) => (channel.messageFrequency);
export const getAmplitude = (channel: FuncGenChannelInterface) => (channel.amplitude);
export const getWaveform = (channel: FuncGenChannelInterface) => (channel.waveform);
export const getPhase = (channel: FuncGenChannelInterface) => (channel.phase);
export const getOutputting = (channel: FuncGenChannelInterface) => (channel.outputting);
export const getModulation = (channel: FuncGenChannelInterface) => (channel.modulation);
