import { isPlatform } from "@ionic/core";

const exportCSV = function (waveValues) {
  //define the heading for each row of the data  
  var csv = 'Point,Value\n';

  //merge the data with CSV  
  for (let row = 0; row < waveValues.length; row++) {
    csv += row + "," + waveValues[row];
    csv += "\n";
  }


  //display the created CSV data on the web browser   
  const csvBlob = new Blob([csv], { type: "text/csv" });
  const blobUrl = URL.createObjectURL(csvBlob);
  const anchorElement = document.createElement("a");

  anchorElement.href = blobUrl;

  var today = new Date();
  var date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
  var time = today.getHours() + "_" + today.getMinutes() + "_" + today.getSeconds();


  var title = "export data - " + date + " at " + time + ".csv";

  const file = new File([csvBlob], "export.csv", { type: "text/csv" });

  const shareData = {
    files: [file],
  };


  if (isPlatform("android")) {
    let targetLoc = 'file:///storage/emulated/0/';

    const failedWarning = function () {
      alert("Save Failed!");
    }

    window.resolveLocalFileSystemURL(targetLoc, function success(filesystem) {
      filesystem.getDirectory('Download', { create: true, exclusive: false }, function (directory) {
        directory.getFile(title, { create: true, exclusive: false }, function (fileEntry) {
          fileEntry.createWriter(function (writer) {
            writer.onwriteend = function () {
              alert("File saved to downloads");
            };
            writer.seek(0);
            writer.write(csv);
          }, failedWarning);
        }, failedWarning);
      }, failedWarning);
    }, failedWarning);


  } else if (isPlatform("ios")) {
    const failedWarning = function (error) {
      if(error && error?.name === "AbortError") return;
      alert('Sharing failed');
    }

    if (navigator.canShare && navigator.canShare(shareData)) {
      navigator.share(shareData)
        .then(() => alert('Share was successful.'))
        .catch(failedWarning);

    }
    else {
      alert("Sharing unsupported");
    }
  } else {
    anchorElement.download = title;
    anchorElement.click();
  }

}
export default exportCSV;