import React, { useState, useContext, useEffect, useCallback, useRef } from 'react';

import {
    IonButtons, IonContent, IonHeader, IonMenuButton, IonPage, IonTitle, IonToolbar,
    IonButton, IonImg, IonPopover, IonInput, IonRange, isPlatform
} from '@ionic/react';

import {
    AppContext, setCarrierFrequencyAction, setMessageFrequencyAction, setAmplitudeAction,
    setCurrChannelAction, setChannelOutputtingAction,
    getCarrierFrequency, getMessageFrequency, getAmplitude, getActiveChannel,
    getTargetChannel, getOutputting, setModulationModeAction, getModulation,
} from '../State';

import type { WAVETYPE } from '../State';

import { FunctionGeneratorAudio } from '../components/FunctionGeneratorAudio';

import './AMFM.css';

const AMFM: React.FC = () => {

    const { state, dispatch } = useContext(AppContext);

    // Effect for disabling channels outputting when leaving page
    useEffect(() => {

        // Cleanup
        return () => {
            dispatch(setChannelOutputtingAction(0, false));
            dispatch(setChannelOutputtingAction(1, false));
        }
    }, [dispatch]);

    const name = "Amplitude Modulation/Frequency Modulation";

    // Selected channel obj
    const activeChannel = getActiveChannel(state);

    // Left-right channels
    const chOne = getTargetChannel(state, 0);
    const chTwo = getTargetChannel(state, 1);

    // Get and set frequency
    const frequency = getCarrierFrequency(activeChannel);
    const setCarrierFrequency = useCallback((freq: number) => {
        dispatch(setCarrierFrequencyAction(freq));
    }, [dispatch]);

    // Get both channel frequencies
    const chOneFrequency = getCarrierFrequency(chOne);
    const chTwoFrequency = getCarrierFrequency(chTwo);

    // Get and set frequency
    const messageFrequency = getMessageFrequency(activeChannel);
    const setMessageFrequency = useCallback((msgFreq: number) => {
        dispatch(setMessageFrequencyAction(msgFreq));
    }, [dispatch]);

    // Get both channel frequencies
    const chOneMessageFrequency = getMessageFrequency(chOne);
    const chTwoMessageFrequency = getMessageFrequency(chTwo);

    // Get and set amplitude
    const amplitude = getAmplitude(activeChannel);
    const setAmplitude = useCallback((amp: number) => {
        dispatch(setAmplitudeAction(Math.max(0, amp)));
    }, [dispatch]);

    // Get both channel amplitude
    const chOneAmplitude = getAmplitude(chOne);
    const chTwoAmplitude = getAmplitude(chTwo);

    // Get and set current channel
    const currChannel = state.currChannel;
    const setCurrChannel = useCallback((channel: 0 | 1) => {
        dispatch(setCurrChannelAction(channel));
    }, [dispatch]);

    // Get and set channels outputting
    const chOneOutputting = getOutputting(chOne);
    const chTwoOutputting = getOutputting(chTwo);

    const toggleChOneOutputting = useCallback(() => {
        dispatch(setChannelOutputtingAction(0, !chOneOutputting));
    }, [chOneOutputting, dispatch]);

    const toggleChTwoOutputting = useCallback(() => {
        dispatch(setChannelOutputtingAction(1, !chTwoOutputting));
    }, [chTwoOutputting, dispatch]);

    const chOneModulation = getModulation(chOne);
    const chTwoModulation = getModulation(chTwo);

    const amChOneModulation = useCallback(() => {
        dispatch(setModulationModeAction(true))
    }, [dispatch]);

    const fmChOneModulation = useCallback(() => {
        dispatch(setModulationModeAction(false))
    }, [dispatch]);

    const amChTwoModulation = useCallback(() => {
        dispatch(setModulationModeAction(true))
    }, [dispatch]);

    const fmChTwoModulation = useCallback(() => {
        dispatch(setModulationModeAction(false))
    }, [dispatch]);

    const minFrequency = 549;

    // For showing and hiding popovers  
    const [frequencyPopoverState, setShowFrequencyPopover] = useState({ showPopover: false, frequency: 440 });
    const [messageFrequencyPopoverState, setShowMessageFrequencyPopover] = useState({ showPopover: false, messageFrequency: 40 });
    const [amplitudePopoverState, setShowAmplitudePopover] = useState({ showPopover: false, amplitude: 100 });

    function popoverFrequencySet() {
        setCarrierFrequency(frequencyPopoverState.frequency);
        setShowFrequencyPopover({ showPopover: false, frequency: frequency });
    }

    function popoverMessageFrequencySet() {
        setMessageFrequency(messageFrequencyPopoverState.messageFrequency);
        setShowMessageFrequencyPopover({ showPopover: false, messageFrequency: messageFrequency });
    }

    function popoverAmplitudeSet() {
        setAmplitude(amplitudePopoverState.amplitude);
        setShowAmplitudePopover({ showPopover: false, amplitude: amplitude });
    }

    function keyUpHandler(event: React.KeyboardEvent<HTMLIonInputElement>, popover: string) {
        if (event.key === "Enter") {
            switch (popover) {
                case "cfrequency": popoverFrequencySet(); break;
                case "mfrequency": popoverMessageFrequencySet(); break;
                case "amplitude": popoverAmplitudeSet(); break;
            }
        }
    }

    const inputFrequency = useRef<HTMLIonInputElement>(null!);
    const inputMessageFrequency = useRef<HTMLIonInputElement>(null!);
    const inputAmplitude = useRef<HTMLIonInputElement>(null!);

    function frequencyInputFocus() {
        inputFrequency.current.setFocus();
    }

    function messageFrequencyInputFocus() {
        inputMessageFrequency.current.setFocus();
    }

    function amplitudeInputFocus() {
        inputAmplitude.current.setFocus();
    }

    const chOneValues = { active: chOneOutputting, frequency: chOneFrequency, messageFrequency: chOneMessageFrequency, amplitude: chOneAmplitude / 100, phase: 0, waveform: "custom" as WAVETYPE, modulation: chOneModulation}
    const chTwoValues = { active: chTwoOutputting, frequency: chTwoFrequency, messageFrequency: chTwoMessageFrequency, amplitude: chTwoAmplitude / 100, phase: 0, waveform: "custom" as WAVETYPE, modulation: chTwoModulation}

    return (
        <IonPage>
            <IonHeader>
                <IonToolbar mode="ios" color="primary">
                    <IonButtons slot="start">
                        <IonMenuButton />
                    </IonButtons>
                    <IonTitle><b style={{fontWeight: "bolder"}}>Husker Scope</b>{": " + name}</IonTitle>
                    <IonImg slot="end" class="logo" src="assets/Husker_Scope_Logo_circle.png"></IonImg>
                </IonToolbar>
            </IonHeader>
            <IonContent fullscreen scrollY={false} color="secondary">
                <FunctionGeneratorAudio chOne={chOneValues} chTwo={chTwoValues} />
                <div className="frequency-waveform-channels-AMFM">

                    {/* Channel 1 Editing Button */}
                    <div className="channel-div-AMFM">
                        <IonButton class="edit-button-L-AMFM" mode="ios" onClick={() => { setCurrChannel(0) }} color={currChannel === 0 ? "primary" : "light"} data-testid="left-edit">
                            <div className="edit-button-contents-AMFM">
                                CH1 <br /> 
                                C-FQ: {Math.round(chOneFrequency)} Hz <br /> 
                                M-FQ: {Math.round(chOneMessageFrequency)}<br/> 
                                AMP: {chOneAmplitude} <br />
                                MODE: {chOneModulation ? "AM" : "FM"} <br />
                            </div>
                        </IonButton>
                    </div>

                    <div className="frequency-and-waveform-AMFM">

                        {/* Frequency Button and Slider */}
                        <div className="frequency-button-div-AMFM">
                            <IonButton class="frequency-button-AMFM" mode="ios" color="light" onClick={() => setShowFrequencyPopover({ showPopover: true, frequency: frequency })} data-testid="frequency-button">
                                Carrier Frequency: {Math.round(frequency)} Hz
                            </IonButton>
                        </div>

                        <div>
                            <IonRange class="range-frequency-AMFM" mode="ios" value={Math.log(frequency - minFrequency)} min={0} max={Math.log(20000 - minFrequency)} step={.0000000001} onIonChange={e => setCarrierFrequency(Math.exp(e.detail.value as any) + minFrequency)} data-testid="frequency-range" />
                        </div>

                        {/* Message Frequency Button and Slider */}
                        <div className="message-frequency-button-div-AMFM">
                            <IonButton class="message-frequency-button-AMFM" mode="ios" color="light" onClick={() => setShowMessageFrequencyPopover({ showPopover: true, messageFrequency: messageFrequency })} data-testid="message-frequency-button">
                                Message Frequency: {Math.round(messageFrequency)} Hz
                            </IonButton>
                        </div>

                        <div>
                            <IonRange class="range-message-frequency-AMFM" mode="ios" value={messageFrequency} min={1} max={frequency / 10} step={.0000000001} onIonChange={e => setMessageFrequency(e.detail.value as any)} data-testid="message-frequency-range" />
                        </div>
                    </div>

                    {/* Channel 2 Editing Button */}
                    <div className="channel-div-AMFM">
                        <IonButton class="edit-button-R-AMFM" mode="ios" onClick={() => { setCurrChannel(1) }} color={currChannel === 1 ? "primary" : "light"} data-testid="right-edit">
                            <div className="edit-button-contents-AMFM">
                                CH2 <br /> 
                                C-FQ: {Math.round(chTwoFrequency)} Hz <br /> 
                                M-FQ: {Math.round(chTwoMessageFrequency)} <br/> 
                                AMP: {chTwoAmplitude} <br />
                                MODE: {chTwoModulation ? "AM" : "FM"} <br />
                            </div>
                        </IonButton>
                    </div>

                </div>

                {/* Amplitude, Start/Stop Buttons, Phase */}
                <div className="amplitude-start-phase-AMFM">

                    {/* Amplitude Button and Slider */}
                    <div className="amplitude-components2-AMFM">
                        <IonButton class="amplitude-button-AMFM" mode="ios" color="light" onClick={() => setShowAmplitudePopover({ showPopover: true, amplitude: amplitude })} data-testid="amplitude-button">
                            Amplitude: {amplitude}%
                        </IonButton>
                        <IonRange class="range-amplitude-AMFM" mode="ios" value={amplitude} min={0} max={100} step={1} onIonChange={e => setAmplitude(e.detail.value as any)} data-testid="amplitude-range" />
                    </div>

                    {/* Play/Pause Buttons */}
                    <div className="start-buttons-AMFM">
                        {/* CH1 Play/Pause Button */}
                        <IonButton class="hold-button-AMFM" mode="ios" onClick={() => { toggleChOneOutputting() }} color={chOneOutputting ? "primary" : "light"} data-testid="left-play">
                            <div className="edit-button-contents-AMFM">
                                CH1
                                <img className="play-pause-img-AMFM" src={chOneOutputting ? "/assets/pause.png" : "/assets/play.png"} alt="" ></img>
                            </div>
                        </IonButton>

                        {/* CH2 Play/Pause Button */}
                        <IonButton class="hold-button-AMFM" mode="ios" onClick={() => { toggleChTwoOutputting() }} color={chTwoOutputting ? "primary" : "light"} data-testid="right-play">
                            <div className="edit-button-contents-AMFM">
                                CH2
                                <img className="play-pause-img-AMFM" src={chTwoOutputting ? "/assets/pause.png" : "/assets/play.png"} alt="" ></img>
                            </div>
                        </IonButton>
                    </div>

                    <div className="AMFM-toggle-div">
                        <IonButton class="modulation-button-AMFM" mode="ios" onClick={() => { currChannel ? amChTwoModulation() : amChOneModulation() }} color={currChannel ? (chTwoModulation ? "primary" : "light") : (chOneModulation ? "primary" : "light")} data-testid="modulation-button">
                            <div className="edit-button-contents-AMFM">
                                AM
                            </div>
                        </IonButton>
                        <IonButton class="modulation-button-AMFM" mode="ios" onClick={() => { currChannel ? fmChTwoModulation() : fmChOneModulation() }} color={currChannel ? (chTwoModulation ? "light" : "primary") : (chOneModulation ? "light" : "primary")} data-testid="modulation-button2">
                            <div className="edit-button-contents-AMFM">
                                FM
                            </div>
                        </IonButton>
                    </div>
                </div>

                {/* Carrier Frequency Popover */}
                <IonPopover
                    isOpen={frequencyPopoverState.showPopover}
                    cssClass={(isPlatform("android") || isPlatform("ios"))? "input-popover-AMFM" : "input-popover-AMFM-web"}
                    mode="ios"
                    showBackdrop={true}
                    onDidPresent={() => frequencyInputFocus()}
                    onDidDismiss={() => setShowFrequencyPopover({ showPopover: false, frequency: frequency })} data-testid="frequency-popup">
                    <div className="popover-div-AMFM">
                        <IonInput ref={inputFrequency} class="input-popover-text-AMFM" type="number" inputmode="numeric" placeholder="550 to 20000 Hz"
                            onIonChange={e => setShowFrequencyPopover((state) => { return { ...state, frequency: parseInt(e.detail.value!, 10) } })}
                            onKeyUp={e => keyUpHandler(e, "cfrequency")}
                        ></IonInput>
                        <IonButton
                            onClick={(e: any) => popoverFrequencySet()} data-testid="frequency-confirm">
                            CONFIRM
                        </IonButton>
                    </div>
                </IonPopover>

                {/* Message Frequency Popover */}
                <IonPopover
                    isOpen={messageFrequencyPopoverState.showPopover}
                    cssClass={(isPlatform("android") || isPlatform("ios"))? "input-popover-AMFM" : "input-popover-AMFM-web"}
                    mode="ios"
                    showBackdrop={true}
                    onDidPresent={() => messageFrequencyInputFocus()}
                    onDidDismiss={() => setShowMessageFrequencyPopover({ showPopover: false, messageFrequency: messageFrequency })} data-testid="message-frequency-popup">
                    <div className="popover-div-AMFM">
                        <IonInput ref={inputMessageFrequency} class="input-popover-text-AMFM" type="number" inputmode="numeric" placeholder={"1 to " + Math.round(frequency / 10) + " Hz"}
                            onIonChange={e => setShowMessageFrequencyPopover((state) => { return { ...state, messageFrequency: parseInt(e.detail.value!, 10) } })}
                            onKeyUp={e => keyUpHandler(e, "mfrequency")}
                        ></IonInput>
                        <IonButton
                            onClick={(e: any) => popoverMessageFrequencySet()} data-testid="message-frequency-confirm">
                            CONFIRM
                        </IonButton>
                    </div>
                </IonPopover>

                {/* Amplitude Popover */}
                <IonPopover
                    isOpen={amplitudePopoverState.showPopover}
                    cssClass={(isPlatform("android") || isPlatform("ios"))? "input-popover-AMFM" : "input-popover-AMFM-web"}
                    mode="ios"
                    showBackdrop={true}
                    onDidPresent={() => amplitudeInputFocus()}
                    onDidDismiss={() => setShowAmplitudePopover({ showPopover: false, amplitude: frequency })} data-testid="amplitude-popup">
                    <div className="popover-div-AMFM">
                        <IonInput ref={inputAmplitude} class="input-popover-text-AMFM" type="number" inputmode="numeric" placeholder="0 to 100"
                            onIonChange={e => setShowAmplitudePopover((state) => { return { ...state, amplitude: parseInt(e.detail.value!, 10) } })}
                            onKeyUp={e => keyUpHandler(e, "amplitude")}
                        ></IonInput>
                        <IonButton onClick={(e: any) => popoverAmplitudeSet()} data-testid="amplitude-confirm">
                            CONFIRM
                        </IonButton>
                    </div>
                </IonPopover>
            </IonContent>
        </IonPage>
    );
};

export default AMFM;