import { IonApp, IonRouterOutlet, IonSplitPane} from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import { Redirect, Route, Switch } from 'react-router-dom';
import { useEffect } from 'react';
import { Plugins } from '@capacitor/core';
import Menu from './components/Menu';
import FunctionGenerator from './pages/FunctionGenerator';
import OScope from './pages/OScope';
import AMFM from './pages/AMFM';
import Tutorial from './pages/Tutorial';
import About from './pages/About';

/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

/* Theme variables */
import './theme/variables.css';

import './App.css';

import { AppContextProvider } from './State';
import { isPlatform, getPlatforms } from '@ionic/core';


const App: React.FC = () => {
  useEffect(() => {
    // Setup handling for backbutton.
    const listener = (ev: Event) => {
      (ev as CustomEvent).detail.register(-1, () => {
        Plugins.App.exitApp();
      })
    };

    document.addEventListener('ionBackButton', listener);

    return () => {
      document.removeEventListener('ionBackButton', listener);
    };
  }, []);

  return (
    <AppContextProvider>
    <IonApp className={isPlatform("android") || isPlatform("ios") ? "" : "web-zoom"} >
      <IonReactRouter basename={process.env.PUBLIC_URL}>
        <IonSplitPane contentId="main" disabled={true}>
          <Menu />
          <IonRouterOutlet mode="md" id="main">
            <Switch>
            <Route path="/" exact={true}>
              <Redirect to="/page/FunctionGenerator" />
            </Route>
            <Route path="/page/FunctionGenerator" exact={true}>
              <FunctionGenerator />
            </Route>
            <Route path="/page/OScope" exact={true}>
              <OScope />
            </Route>
            <Route path="/page/AMFM" exact={true}>
            <AMFM />
            </Route>
            <Route path="/page/Tutorial" exact={true}>
              <Tutorial />
            </Route>
            <Route path="/page/About" exact={true}>
              <About />
            </Route>
            </Switch>
          </IonRouterOutlet>
        </IonSplitPane>
      </IonReactRouter>
    </IonApp>
    </AppContextProvider>
  );
};

export default App;
