import React, { useRef, useEffect } from 'react';

import { FunctionGeneratorChannel } from './FunctionGeneratorChannel';

import type { FGAudioChannel } from './FunctionGeneratorChannel';

import { getSyncMultiplier } from 'utils/FrequencyUtils';

import WakeLock from 'components/WakeLock';

export interface FGAudioProps {
  chOne: FGAudioChannel,
  chTwo: FGAudioChannel,
}

export const FunctionGeneratorAudio = (props: FGAudioProps) => {
  const audioCtx = useRef<AudioContext>(null!);

  // Init/cleanup audio context
  useEffect(() => {
    audioCtx.current = new AudioContext();

    return () => {
      audioCtx.current.close();
    };
  }, []);

  // Check if should sync
  const syncMultiplier = getSyncMultiplier(props.chOne.frequency, props.chTwo.frequency);

  // Create the two channels
  return (
    <>
      <WakeLock enabled={props.chOne.active || props.chTwo.active} />
      <FunctionGeneratorChannel channel="left" syncMultiplier={syncMultiplier} audioCtx={audioCtx.current} {...props.chOne} />
      <FunctionGeneratorChannel channel="right" syncMultiplier={syncMultiplier} audioCtx={audioCtx.current} {...props.chTwo} />
    </>
  );
};
