// Get the multiplier of the larger freq w.r.t. smaller or 0 if not divisible.
export function getSyncMultiplier(freqOne: number, freqTwo: number): number {
  // Is one number a multiple of the other?
  let remainder = Math.max(freqOne, freqTwo) % Math.min(freqOne, freqTwo);
  
  // Not a multiplier, return 0;
  if(remainder !== 0) {
    return 0;
  }

  // Otherwise, return multiplicity of larger frequency
  return Math.max(freqOne, freqTwo) / Math.min(freqOne, freqTwo);
}
