import React, { useEffect } from 'react';
import { Insomnia } from '@awesome-cordova-plugins/insomnia';

interface WakeLockProps {
  enabled: boolean;
}

const WakeLock = (props: WakeLockProps) => {

  useEffect(() => {
    if(props.enabled) {
      // Wake-lock on
      Insomnia.keepAwake();

      // Disable wakelock
      return () => {
        Insomnia.allowSleepAgain();
      };
    }

  }, [props.enabled]);

  return null;
}

export default WakeLock;
