import Paper from "paper";
import exportCSV from 'components/exportCSV';

let drawingWave = false;
let voltageDiv = .5;
let timeDiv = 1;

let timeDiff = 0.5;
let voltDiff = 0;

let autoset = false;
let maxPoint = 0;
let frequency = 0;

let mobile = false;

const bufferSize = 8192;

let sampleRate = 44100;

//.707 VRMS = -1 to 1 V AC
//No resistance (no adapter): 149 Hz
//With resistance (adapter): 91 Hz
const voltageMultiplierConstant = .5 * .9;

let triggerPoint = null;
let triggerFlag = null;
let saveFlag = false;

export const setDrawWave = (val) => drawingWave = val;
export const setVoltageDiv = (val) => voltageDiv = val;
export const setTimeDiv = (val) => timeDiv = val;
export const setTriggers = (val1, val2) => { timeDiff = val1; voltDiff = val2; }
export const setAutoset = (val) => autoset = val;
export const setTriggerFlag = (val) => triggerFlag = val;
export const setSaveFlag = (val) => saveFlag = val;


const drawWave = function (frequencyChangeCallback, autosetFinishCallback, timeDivChangeCallback, voltDivChangeCallback, saveFinishCallback) {

  let waveLayer = new Paper.Layer();

  // Input buffer
  let dataArray = new Float32Array(bufferSize);
  let bufferIdx = 0;

  let findTrigger = () => {
    triggerPoint = null;
    for(let i = Math.floor(dataArray.length/2); i < dataArray.length-1; i++){
      let point1 = dataArray[i];
      let point2 = dataArray[i+1];
      //Rising Edge
      if(triggerFlag === true){
        if(point1 <= (voltDiff*voltageDiv)/voltageMultiplierConstant){
          if(point2 > (voltDiff*voltageDiv)/voltageMultiplierConstant){
            triggerPoint = i;
            break;
          }
        }
      }else {
        //Falling Edge 
        if(point1 >= (voltDiff*voltageDiv)/voltageMultiplierConstant){
          if(point2 < (voltDiff*voltageDiv)/voltageMultiplierConstant){
            triggerPoint = i;
            break;
          }
        }
      }
      
    }
  };

  let findMaxVoltage = () => {
    maxPoint = .01;
    for(let i = 0; i < dataArray.length; i++){
      let point = Math.abs(dataArray[i]);
      if (point > maxPoint) {
        maxPoint = point;
      }
    }
  }

  let findFrequency = () => {
    let crossCount = 0;
    let firstIndex = -1;
    let lastIndex = -1;
    for(let i = 0; i < dataArray.length - 1; i++){
      let point1 = dataArray[i];
      let point2 = dataArray[i+1];
      if (point1 === 0 || point1 < 0 && point2 > 0 || point1 > 0 && point2 < 0) {
        if (crossCount === 0) {
          firstIndex = i;
        }
        lastIndex = i;
        crossCount++;
      }
    }

    if (firstIndex === -1) {
      frequency = 0;
    } else {
      frequency = sampleRate * .5 * (crossCount - 1) / (lastIndex - firstIndex);
    }

    frequencyChangeCallback(Math.round(frequency));
  }

  if (window.audioinput) {
    function startCapture() {
      window.audioinput.start({
        streamToWebAudio: false, bufferSize: bufferSize
      });
    }

    //audio input update
    window.addEventListener("audioinput", (evt) => {
      // Skip input if not drawing
      if(drawingWave) {
        bufferIdx = 0;
        dataArray = evt.data;
      }

      findTrigger();
      findMaxVoltage();
      findFrequency();
    });


    window.audioinput.checkMicrophonePermission(function (hasPermission) {
      
      if (hasPermission) {
        console.log("We already have permission to record.");        
        startCapture();
      } else {
        window.audioinput.getMicrophonePermission(function (hasPermission, message) {
          if (hasPermission) {
            console.log("User granted us permission to record.");
            startCapture();
          } else {
            console.log("Failed to obtain capture permission");
          }
        });
      }
    });
  } else {
    var constraints = { audio: {autoGainControl: false, echoCancellation: false,  noiseSuppression: false}};

    navigator.mediaDevices.getUserMedia(constraints)
    .then(function(mediaStream) {
      let ctx = new AudioContext({sampleRate: 44100});
      sampleRate = ctx.sampleRate;
      let mic = ctx.createMediaStreamSource(mediaStream);
      let spe = ctx.createAnalyser();
      spe.fftSize = bufferSize;
      spe.getFloatTimeDomainData(dataArray);
      mic.connect(spe);

      let osc = ctx.createOscillator();
      osc.type = 'sine';
      osc.frequency.value = 440;
      //osc.connect(spe);
      //osc.start();
      //spe.connect(ctx.destination);

      setInterval(() => {
        // Skip if not drawing
        if(drawingWave) {
          bufferIdx = 0;
          spe.getFloatTimeDomainData(dataArray);
        }

        findTrigger();
        findMaxVoltage();
        findFrequency();
      }, 20)
    })
    .catch(function(err) { console.log(err.name + ": " + err.message); }); // always check for errors at the end.
  }

  function Wave() {
    this.amount = 200;
    this.height = 10;
    this.offsetX = 0;
    this.waveSpeed = 3;
    this.selected = false;

    this._path = new Paper.Path({ fillColor: this.color });

    this.init = function () {
      this._path.strokeColor = this.color;
      this._path.selected = this.selected;

      this._path.segments = [];

      // Cover entire horizontal screen space with points
      for (let i = 0; i <= this.amount; i++) {
        let point = new Paper.Point(i / this.amount * Paper.view.size.width,
          1 / 2 * Paper.view.size.height);
        this._path.add(point);
      }
    }

    this.update = function (_event) {
      let height = Paper.view.size.height;

      if (autoset) {
        let numOfIterations = 0;

        let tempVoltDiv = Math.min(Math.max(Math.round(.5 * 10 * maxPoint) / 10, .1), 4.9);

        let tempTimeDiv = 1;
        let divider = .4536;
        if (frequency < 440) {
          let timeDividerConstant = 400;
          tempTimeDiv = Math.round((timeDividerConstant / frequency)/divider);
        }

        autoset = false;
        timeDivChangeCallback(tempTimeDiv);
        voltDivChangeCallback(tempVoltDiv);
        autosetFinishCallback();
      }

      if(saveFlag){
        exportCSV(dataArray, mobile);
        saveFlag = false;
        saveFinishCallback();
      }

      // Loop through every second point of the path to make waves:
      for (let i = 0; i <= this.amount; i++) {
        let segment = this._path.segments[i];
      
        let startPoint = bufferIdx;
        let timeOffset = 0;
        if(triggerPoint != null){
          startPoint = triggerPoint;
          timeOffset = -((timeDiff * this.amount * timeDiv) | 0);
        }

        // Just use latest buffer data for now :)
        let idx = timeOffset + startPoint + (i * timeDiv);
        let bufferPoint = dataArray[idx];

        // Flip vertically for rendering
        bufferPoint = -bufferPoint / voltageDiv * voltageMultiplierConstant;

        // Handle the data
        segment.point.y = (bufferPoint + 1) * (height / 2);
      }


      if(drawingWave) {
        bufferIdx += this.amount + 1;
        bufferIdx %= dataArray.length;
      }
      //this._path.smooth({ from: 0, to: this.amount });
    }

  }

  var numOfWBoxes = 10;
  var numOfHBoxes = 8;

  var path1 = new Wave();
  path1.color = 'blue';
  path1.height = 10;
  //path1.selected = true;
  path1.init();


  Paper.view.on('frame', function (event) {
    waveLayer.activate();
    path1.update(event);
  });
  

  Paper.view.on('resize', function (event) {
    waveLayer.activate();
    path1.init();
  });

};

export default drawWave;
